.root {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.8em;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.rootVertical {
  composes: root;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.rootHorizontal {
  composes: root;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.label {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  margin-bottom: 0.2em;
}

.barContainer {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  width: 100%;
  margin-bottom: 0.2em;
}

.track,
.bar {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.track {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.04);
  border: 1px solid rgba(0, 0, 0, 0.06);
}

.bar {
  z-index: 2;
  background-color: #000;
}



