@import 'css-reset/reset.css';

:global(html) {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
}

:global(*),
:global(*::before),
:global(*::after) {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

:global(html),
:global(body) {
  width: 100%;
}

:global(body) {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}



